h1 {
  text-align: center;
}

.scoreboard {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 24px;
}
.upper-score,
.lower-score {
  background: #eaf6fb;
  border-radius: 18px;
  box-shadow: 0 2px 12px rgba(42, 82, 152, 0.08);
  width: 50%;
  margin: 0 5px;
  padding: 18px 10px 10px 10px;
}

.upper-square {
  height: 48px;
  width: 48px;
  border-radius: 8px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.06);
  background: #fff;
  object-fit: contain;
  display: block;
  margin: 0 auto;
}

.roll-dice-btn {
  border: none;
  color: white;
  margin-bottom: 12px;
  margin-top: 32px; /* Add space above the roll dice button */
}

.roll-dice-btn button {
  background: linear-gradient(90deg, #2a5298 0%, #1e3c72 100%);
  color: #fff;
  border: none;
  border-radius: 12px;
  padding: 10px 28px;
  font-size: 1.1rem;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(42, 82, 152, 0.12);
  transition: background 0.2s, transform 0.2s;
}
.roll-dice-btn button:hover:not(:disabled) {
  background: linear-gradient(90deg, #1e3c72 0%, #2a5298 100%);
  transform: translateY(-2px) scale(1.04);
}
.roll-dice-btn:disabled,
.roll-dice-btn button:disabled {
  background: #dddddd;
  color: #aaaaaa;
  cursor: not-allowed;
}

.dice {
  display: flex;
  justify-content: center;
  gap: 18px;
  background: transparent;
  margin-bottom: 18px;
}

.square,
.held-die,
.normal {
  width: 100%;
  display: inline-block;
}

.held-die {
  background-color: #b2d7f3;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(42, 82, 152, 0.18);
}

.selected {
  background-color: #d6eaff !important;
  font-weight: 600;
}

table,
th,
td {
  text-align: left;
  font-family: 'Segoe UI', Arial, sans-serif;
  border: 1px solid #b2d7f3;
  border-radius: 6px;
  background: #fff;
  padding: 6px 10px;
}
table {
  width: 100%;
  margin-bottom: 12px;
  border-collapse: separate;
  border-spacing: 0;
  box-shadow: 0 2px 8px rgba(42, 82, 152, 0.08);
}
th {
  background: #2a5298;
  color: #fff;
  font-weight: 700;
  font-size: 1.05rem;
}

td img.upper-square {
  vertical-align: middle;
}

.game-over-popup {
  justify-content: center;
  transform: translate(-50%, -50%);
  background: #eaf6fb;
  padding: 32px 18px;
  border-radius: 18px;
  box-shadow: 0 2px 12px rgba(42, 82, 152, 0.18);
  z-index: 1;
  color: #2a5298;
  text-align: center;
  font-size: 1.3rem;
}

.game-over-popup button {
  background: linear-gradient(90deg, #2a5298 0%, #1e3c72 100%);
  color: #fff;
  border: none;
  border-radius: 10px;
  padding: 8px 24px;
  font-size: 1.1rem;
  font-weight: 600;
  margin-top: 18px;
  box-shadow: 0 2px 8px rgba(42, 82, 152, 0.12);
  transition: background 0.2s, transform 0.2s;
}
.game-over-popup button:hover {
  background: linear-gradient(90deg, #1e3c72 0%, #2a5298 100%);
  transform: translateY(-2px) scale(1.04);
}
